    clip: rect(1px, 1px, 1px, 1px);
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    white-space: nowrap;
}

/* Hard to override styles */

:global(.filepond--browser.filepond--browser) {
    /* is positioned absolute so it is focusable for form validation errors */
    position: absolute;
    margin: 0;
