    height: 0px;

    /* dont allow selection */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    /* will be animated */
    will-change: transform, opacity;
}

/* Hard to override styles on purpose */

:global(.filepond--drop-label.filepond--drop-label) :global(label) {
    display: block;
    margin: 0;
    padding: 0.5em; /* use padding instead of margin so click area is not impacted */
}

:global(.filepond--drop-label) :global(label) {
    cursor: default;
    font-size: 0.875em;
