/* edit button */

:global(.filepond--action-edit-item.filepond--action-edit-item) {
    width: 2em;
    height: 2em;
    padding: 0.1875em;
}

:global(.filepond--action-edit-item.filepond--action-edit-item[data-align*='center']) {
