    /* no margins on children */
}

:global(.filepond--file-status) :global(*) {
    margin: 0;
    white-space: nowrap;
}

:global(.filepond--file-status) {
    /* font sizes */
}

:global(.filepond--file-status) :global(.filepond--file-status-main) {
    font-size: 0.75em;
    line-height: 1.2;
}

:global(.filepond--file-status) :global(.filepond--file-status-sub) {
