}

/* Hard to override styles */

:global(.filepond--file-wrapper.filepond--file-wrapper) {
    border: none;
    margin: 0;
    padding: 0;
    min-width: 0;
    height: 100%;

    /* hide legend for visual users */
}

:global(.filepond--file-wrapper.filepond--file-wrapper) > :global(legend) {
    position: absolute;
