}

:global(.filepond--file) {
    /* .filepond--file-action-button */
}

:global(.filepond--file) :global([data-align*='left']) {
    left: 0.5625em;
}

:global(.filepond--file) :global([data-align*='right']) {
    right: 0.5625em;
}

:global(.filepond--file) :global([data-align*='center']) {
    left: calc(50% - 0.8125em); /* .8125 is half of button width */
}

:global(.filepond--file) :global([data-align*='bottom']) {
    bottom: 1.125em;
}

:global(.filepond--file) :global([data-align='center']) {
    top: calc(50% - 0.8125em);
}

:global(.filepond--file) :global(.filepond--progress-indicator) {
    margin-top: 0.1875em;
}

:global(.filepond--file) :global(.filepond--progress-indicator[data-align*='right']) {
    margin-right: 0.1875em;
}

:global(.filepond--file) :global(.filepond--progress-indicator[data-align*='left']) {
    margin-left: 0.1875em;
}

/* make sure text does not overlap */

:global([data-filepond-item-state='cancelled']) :global(.filepond--file-info),
:global([data-filepond-item-state*='invalid']) :global(.filepond--file-info),
:global([data-filepond-item-state*='error']) :global(.filepond--file-info) {
    margin-right: 2.25em;
}

:global([data-filepond-item-state~='processing']) :global(.filepond--file-status-sub) {
    opacity: 0;
}

:global([data-filepond-item-state~='processing']
)    :global(.filepond--action-abort-item-processing
)    ~ :global(.filepond--file-status
