}

:global([data-filepond-item-state='processing-error']) :global(.filepond--file-status-sub) {
    opacity: 0;
}

:global([data-filepond-item-state='processing-error']
)    :global(.filepond--action-retry-item-processing
)    ~ :global(.filepond--file-status
)    :global(.filepond--file-status-sub) {
    opacity: 0.5;
}

:global([data-filepond-item-state='processing-complete']) {
    /* busy state */
}

:global([data-filepond-item-state='processing-complete']) :global(.filepond--action-revert-item-processing) :global(svg) {
    -webkit-animation: fall 0.5s 0.125s linear both;
    animation: fall 0.5s 0.125s linear both;
}

:global([data-filepond-item-state='processing-complete']) {
    /* hide details by default, only show when can revert */
}

:global([data-filepond-item-state='processing-complete']) :global(.filepond--file-status-sub) {
    opacity: 0.5;
}

:global([data-filepond-item-state='processing-complete']
)    :global(.filepond--processing-complete-indicator:not([style*='hidden'])
)    ~ :global(.filepond--file-status
)    :global(.filepond--file-status-sub) {
    opacity: 0;
}

:global([data-filepond-item-state='processing-complete']) :global(.filepond--file-info-sub) {
    opacity: 0;
}

:global([data-filepond-item-state='processing-complete']
)    :global(.filepond--action-revert-item-processing
)    ~ :global(.filepond--file-info
)    :global(.filepond--file-info-sub) {
    opacity: 0.5;
}

/* file state can be invalid or error, both are visually similar but */

/* having them as separate states might be useful */

:global([data-filepond-item-state*='invalid']) :global(.filepond--panel),
:global([data-filepond-item-state*='invalid']) :global(.filepond--file-wrapper),
:global([data-filepond-item-state*='error']) :global(.filepond--panel),
:global([data-filepond-item-state*='error']) :global(.filepond--file-wrapper) {
    -webkit-animation: shake 0.65s linear both;
    animation: shake 0.65s linear both;
}

/* spins progress indicator when file is marked as busy */

:global([data-filepond-item-state*='busy']) :global(.filepond--progress-indicator) :global(svg) {
    -webkit-animation: spin 1s linear infinite;
    animation: spin 1s linear infinite;
}

/**
 * States
 */

@-webkit-keyframes -global-spin {
    0% {
        -webkit-transform: rotateZ(0deg);
        transform: rotateZ(0deg);
    }

    100% {
        -webkit-transform: rotateZ(360deg);
        transform: rotateZ(360deg);
    }
}

@keyframes -global-spin {
    0% {
        -webkit-transform: rotateZ(0deg);
        transform: rotateZ(0deg);
    }

    100% {
        -webkit-transform: rotateZ(360deg);
        transform: rotateZ(360deg);
    }
}

@-webkit-keyframes -global-shake {
    10%,
    90% {
        -webkit-transform: translateX(-0.0625em);
        transform: translateX(-0.0625em);
    }

    20%,
    80% {
        -webkit-transform: translateX(0.125em);
        transform: translateX(0.125em);
    }

    30%,
    50%,
    70% {
        -webkit-transform: translateX(-0.25em);
        transform: translateX(-0.25em);
    }

    40%,
    60% {
        -webkit-transform: translateX(0.25em);
        transform: translateX(0.25em);
    }
}

@keyframes -global-shake {
    10%,
    90% {
        -webkit-transform: translateX(-0.0625em);
        transform: translateX(-0.0625em);
    }

    20%,
    80% {
        -webkit-transform: translateX(0.125em);
        transform: translateX(0.125em);
    }

    30%,
    50%,
    70% {
        -webkit-transform: translateX(-0.25em);
        transform: translateX(-0.25em);
    }

    40%,
    60% {
        -webkit-transform: translateX(0.25em);
        transform: translateX(0.25em);
    }
}

@-webkit-keyframes -global-fall {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
    }

    70% {
        opacity: 1;
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
        -webkit-animation-timing-function: ease-in-out;
        animation-timing-function: ease-in-out;
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
    }
}

@keyframes -global-fall {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
    }

    70% {
        opacity: 1;
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
        -webkit-animation-timing-function: ease-in-out;
        animation-timing-function: ease-in-out;
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-animation-timing-function: ease-out;
        animation-timing-function: ease-out;
    }
}

/* ignore all other interaction elements while dragging a file */

:global(.filepond--hopper[data-hopper-state='drag-over']) > :global(*) {
    pointer-events: none;
}

/* capture all hit tests using a hidden layer, this speeds up the event flow */

:global(.filepond--hopper[data-hopper-state='drag-over']::after) {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
}

:global(.filepond--progress-indicator) {
    z-index: 103;
}

:global(.filepond--file-action-button) {
    z-index: 102;
}

:global(.filepond--file-status) {
    z-index: 101;
}

:global(.filepond--file-info) {
    z-index: 100;
}

:global(.filepond--item) {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;

    padding: 0;
    margin: 0.25em;

    will-change: transform, opacity;

    /* item children order */
}

:global(.filepond--item) > :global(.filepond--panel) {
    z-index: -1;
}

/* has a slight shadow */

:global(.filepond--item) > :global(.filepond--panel) :global(.filepond--panel-bottom) {
    box-shadow: 0 0.0625em 0.125em -0.0625em rgba(0, 0, 0, 0.25);
}

:global(.filepond--item) {
    /* drag related */
}

:global(.filepond--item) > :global(.filepond--file-wrapper),
:global(.filepond--item) > :global(.filepond--panel) {
    transition: opacity 0.15s ease-out;
}

:global(.filepond--item[data-drag-state]) {
    cursor: -webkit-grab;
    cursor: grab;
}

:global(.filepond--item[data-drag-state]) > :global(.filepond--panel) {
    transition: box-shadow 0.125s ease-in-out;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
}

:global(.filepond--item[data-drag-state='drag']) {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

:global(.filepond--item[data-drag-state='drag']) > :global(.filepond--panel) {
    box-shadow: 0 0.125em 0.3125em rgba(0, 0, 0, 0.325);
}

:global(.filepond--item[data-drag-state]:not([data-drag-state='idle'])) {
    z-index: 2;
}

/* states */

:global(.filepond--item-panel) {
    background-color: #64605e;
}

:global([data-filepond-item-state='processing-complete']) :global(.filepond--item-panel) {
    background-color: #369763;
}

:global([data-filepond-item-state*='invalid']) :global(.filepond--item-panel),
:global([data-filepond-item-state*='error']) :global(.filepond--item-panel) {
    background-color: #c44e47;
}

/* style of item panel */

:global(.filepond--item-panel) {
    border-radius: 0.5em;
    transition: background-color 0.25s;
}

/* normal mode */

:global(.filepond--list-scroller) {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0;
    will-change: transform;
}

/* scroll mode */

:global(.filepond--list-scroller[data-state='overflow']) :global(.filepond--list) {
    bottom: 0;
    right: 0;
}

:global(.filepond--list-scroller[data-state='overflow']) {
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    -webkit-mask: linear-gradient(to bottom, #000 calc(100% - 0.5em), transparent 100%);
    mask: linear-gradient(to bottom, #000 calc(100% - 0.5em), transparent 100%);
}

/* style scrollbar */

:global(.filepond--list-scroller::-webkit-scrollbar) {
    background: transparent;
}

:global(.filepond--list-scroller::-webkit-scrollbar:vertical) {
    width: 1em;
}

:global(.filepond--list-scroller::-webkit-scrollbar:horizontal) {
    height: 0;
}

:global(.filepond--list-scroller::-webkit-scrollbar-thumb) {
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 99999px;
    border: 0.3125em solid transparent;
    background-clip: content-box;
}

/* hard to overide styles on purpose */

:global(.filepond--list.filepond--list) {
    position: absolute;
    top: 0;
    margin: 0;
    padding: 0;
    list-style-type: none;

    /* prevents endless paint calls on filepond--list-scroller */
    will-change: transform;
}

/* used for padding so allowed to be restyled */

:global(.filepond--list) {
    left: 0.75em;
    right: 0.75em;
}

:global(.filepond--root[data-style-panel-layout~='integrated']) {
    width: 100%;
    height: 100%;
    max-width: none;
    margin: 0;
}

:global(.filepond--root[data-style-panel-layout~='circle']) :global(.filepond--panel-root),
:global(.filepond--root[data-style-panel-layout~='integrated']) :global(.filepond--panel-root) {
    border-radius: 0;
}

:global(.filepond--root[data-style-panel-layout~='circle']) :global(.filepond--panel-root) > :global(*),
:global(.filepond--root[data-style-panel-layout~='integrated']) :global(.filepond--panel-root) > :global(*) {
    display: none;
}

:global(.filepond--root[data-style-panel-layout~='circle']) :global(.filepond--drop-label),
:global(.filepond--root[data-style-panel-layout~='integrated']) :global(.filepond--drop-label) {
    bottom: 0;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 7;
}

:global(.filepond--root[data-style-panel-layout~='circle']),
:global(.filepond--root[data-style-panel-layout~='integrated']) {
    /* we're only loading one item, this makes the intro animation a bit nicer */
}

:global(.filepond--root[data-style-panel-layout~='circle']) :global(.filepond--item-panel),
:global(.filepond--root[data-style-panel-layout~='integrated']) :global(.filepond--item-panel) {
